.modal__backdrop {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
}

.modal__box {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    height: 300px;
    width: 300px;
    padding: 40px;
    background: linear-gradient(135deg, #5fc3e4 0%, #e55d87 100%);
    border-radius: 10px;
    box-shadow: 0px 2px 1px -1px rgba(0, 0, 0, 0.2), 0px 1px 1px 0px rgba(0, 0, 0, 0.14), 0px 1px 3px 0px rgba(0, 0, 0, 0.12);
}

.form {
    display: flex;
    flex-direction: column;
    gap: 40px;
}


.form button {
    margin: 0 auto;
    width: 100px;
    border: double 5px #e55d87;
    border-radius: 20px;
    background-color: inherit;
    transition: ease-in-out transform 100ms border;
}

.form button:hover {
    transform: scale(1.1);
    border: double 5px #5fc3e4;
}